/* width */

::-webkit-scrollbar {
  width: 10px;
}

/* Track */

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 35px;
  box-shadow: inset 0 0 5px rgb(216, 216, 216);
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: rgb(167, 167, 167);
  border-radius: 35px;
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
